<template>
  <div class="wrapper">
    <div class="encrypt">
      <div class="inputData">
        <button @click="deleteData">모두삭제</button>
        <h3>RSA 암호화 _ JSEncrypt 사용</h3><br/>
        <form action="">
        <input type="text" v-model="preventDataEncryption" placeholder="입력">
        <button @click="encryptData">암호화</button><br/>
        </form>
      </div>
      <div class="encryption">
        <h3>preventDataEncryption : {{preventDataEncryption}}</h3>
        <textarea cols="30" rows="10" v-model="encData"></textarea>
        <h3>dataLength : {{dataLength}}</h3>
        키 길이에 따라 다름 현재 515bit 사용
      </div>
    </div>
    <hr>
    <div class="decrypt">
      <textarea cols="30" rows="10" v-model="preventDataDecryption" placeholder="암호화된 데이터 입력"></textarea>
      <button @click="decryptData">복호화</button>
      <h3>decryptData : {{decryptedData}}</h3>
    </div>
  </div>
</template>

<script>
// import CryptoJS from "crypto-js";
import {JSEncrypt} from "jsencrypt";

export default {
  name: 'JSEncryptRSA',
  data() {
    return {
      preventDataEncryption: '',
      preventDataDecryption: '',
      dataLength: 0,
      // TODO:  key 다른 곳에 저장하기
      privateKey: "",
      publickKey: "MFwwDQYJKoZIhvcNAQEBBQADSwAwSAJBALCoc/KXb7licTXcW+7i4UmCE5cinhk6p2gGAYfeFT15VbMFCrI/Jc3atFo6ZNimcBQRHHCJJR3/K9V1bz/zgrcCAwEAAQ==",
      decryptedData: '',
      encData: '',
    }
  },
  // mounted() {
  //   console.warn(process.env.VUE_APP_RSA_PRIVATE_KEY)
  // },
  methods: {
    encryptData(e){
      e.preventDefault();

      if(this.preventDataEncryption !== ''){

        const encrytor = new JSEncrypt();
        // 공개키 설정
        encrytor.setPublicKey(this.publickKey);
        const data = encrytor.encrypt(this.preventDataEncryption);
        localStorage.setItem("secretData", data); //암호화 된 data를 storage에 저장
        this.encData = data;
        this.dataLength = (data.toString().length);
      }
    },
    decryptData(e) {
      e.preventDefault();
      // const secretData = localStorage.getItem("secretData"); // storage에서 암호화된 data 꺼내옴
      if(this.preventDataEncryption !== '' && this.preventDataDecryption !== null){
        const decrypt = new JSEncrypt();
        decrypt.setPrivateKey(this.privateKey);
        const decryptData = decrypt.decrypt(this.preventDataDecryption);
        this.decryptedData = decryptData;
      }
    },
    deleteData(e) {
      e.preventDefault();
      localStorage.removeItem("secretData");
      this.preventDataEncryption = '';
      this.preventDataDecryption = '';
      this.decryptedData = '';
      this.dataLength = 0;
      this.getEncryptedData();
    },
    getEncryptedData() {
      this.encData = localStorage.getItem("secretData") || "";
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 20px 0 0;
}
.wrapper{
  display: flex;
  align-items: center;
//flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}
.encrypt, .decrypt{
  width: 3000px;
  margin: 30px;
}
</style>
