<template>
  <div class="wrapper">
    <div class="encrypt">
      <div class="inputData">
        <button @click="deleteData">모두삭제</button>
        <h3>AES 암호화 _ CryptoJS 사용</h3><br/>
        <input type="text" v-model="preventDataEncryption" placeholder="평문 입력">
        <button @click="encryptData">암호화</button>
      </div>
      <div class="encryptionData">
        <h3>preventDataEncryption : {{preventDataEncryption}}</h3>
        <textarea cols="30" rows="10" v-model="encData"></textarea>
        <h3>dataLength : {{dataLength}}</h3>
      </div>
    </div>
    <hr>
    <div class="decrypt">
      <h3>AES 복호화 _ CryptoJS 사용</h3><br/>
      <textarea cols="30" rows="10" v-model="preventDataDecryption" placeholder="암호화된 데이터 입력"></textarea><br/>
      <button @click="decryptData">복호화</button>
      <h3>decryptData : {{decryptedData}}</h3>
    </div>
  </div>
</template>

<script>
//https://cryptojs.gitbook.io/docs/
import CryptoJS from "crypto-js";
export default {
  name: 'CryptoAes',
  data() {
    return {
      key: '',
      iv: '',
      preventDataEncryption: '',
      preventDataDecryption: '',
      dataLength: 0,
      secretPassphrase: '0123456789abcdef', //key값 16바이트
      decryptedData: '',
      encData: '',
    }
  },
  methods: {
    encryptData(e){
      e.preventDefault();
      this.key = CryptoJS.lib.WordArray.random(32).toString();
      this.iv = CryptoJS.lib.WordArray.random(16).toString();
      if(this.preventDataEncryption !== ''){
        const data = CryptoJS.AES.encrypt(this.preventDataEncryption, this.key, {
          iv: this.iv,
          mode: CryptoJS.mode.CBC,
          padding: CryptoJS.pad.Pkcs7,
        }).toString();
        this.encData = data;
        this.dataLength = data.length;
      }
    },
    decryptData() {
      if(this.preventDataEncryption !== '' && this.preventDataDecryption !== null){
        const decryptData = this.CryptoJS.AES.decrypt(this.preventDataDecryption, this.key, {
          iv: this.iv,
          mode: CryptoJS.mode.CBC,
          padding: CryptoJS.pad.Pkcs7,
        }).toString(this.CryptoJS.enc.Utf8);
        this.decryptedData = decryptData;
      }
    },

    deleteData() {
      // localStorage.removeItem("secretData");
      this.preventDataEncryption = '';
      this.preventDataDecryption = '';
      this.decryptedData = '';
      this.dataLength = 0;
      this.getEncryptedData();
    },
    getEncryptedData() {
      this.encData = localStorage.getItem("secretData") || "";
    },
  }
}
</script>

